import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { ThemeProvider, createTheme } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import Container from '@mui/material/Container';

// Import pages
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import DownloadsPage from './pages/DownloadsPage';
import Century21Analysis from './pages/Century21Analysis'; // Import the new page

// Import components
import Header from './components/Header';
import Footer from './components/layout/Footer'; // Fixed import path

// Create theme
const theme = createTheme({
  palette: {
    primary: {
      main: '#354CA1', // SMU blue
    },
    secondary: {
      main: '#C8102E', // SMU red
    },
    background: {
      default: '#f5f5f5',
    }
  },
  typography: {
    fontFamily: '"Arial", "Helvetica", sans-serif',
    h1: {
      fontWeight: 700,
    },
    h2: {
      fontWeight: 600,
    },
    h4: {
      fontWeight: 600,
    },
  },
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router basename="/DS_6371_Project">
        <Header />
        <Container component="main" sx={{ mt: 8, mb: 8 }}>
          <Routes>
            <Route path="/" element={<HomePage />} />
            <Route path="/about" element={<AboutPage />} />
            <Route path="/downloads" element={<DownloadsPage />} />
            <Route path="/century21-analysis" element={<Century21Analysis />} /> {/* Add the new route */}
          </Routes>
        </Container>
        <Footer /> {/* Restored Footer component */}
      </Router>
    </ThemeProvider>
  );
}

export default App;
